export default new Vuex.Store({
  state: {
    drawer: null,    // 定义用于控制导航菜单收缩或展开的状态
  },
  mutations: {
    // 用于修改 drawer状态的方法
    SET_DRAWER(state, payload){
      state.drawer = payload;
    },
  },
  actions: {
  },
  modules: {
  }
})
