import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    // name: 'Home',
    component: () => import('@/views/dashboard/Index.vue'),
    children: [
      {
        path: '',   // 当前父路由的默认值
        redirect: 'dashboard',   // 路由重定向
      },
      {
        path: 'dashboard',   // 当前父路由的默认值
        name: 'Dashboard',
        component: () => import('@/views/dashboard/pages/Dashboard.vue'),
      },
      {
        path: 'pages/photos',
        name: 'Photos',
        component: () => import('@/views/dashboard/pages/Photos.vue'),
      },

      {
        path: 'products/index',
        name: 'Products',
        component: () => import('@/views/products/Index.vue'),
      },
      {
        path: 'products/create',
        name: 'Create',
        component: () => import('@/views/products/Create.vue'),
      },
      {
        path: 'products/category',
        name: 'Category',
        component: () => import('@/views/products/Category.vue'),
      },
      {
        path: 'products/property',
        name: 'Property',
        component: () => import('@/views/products/Property.vue'),
      }, 
      {
        path:'product/:id',
        name:'Property',
        component:() => import('@/views/products/Details.vue')
      }
    ]
  },
  {
    path: '/about',
    name: 'About',
    component: () => import('../views/About.vue')
  },
  {
    path:'/account',
    component:()=>import('@/views/account/Index.vue'),
    children:[
      {
        path:'',
        redirect:'login',  // 路由重定向
      },
      {
        path:'login',
        name:'Login',
        component:()=>import('@/views/account/pages/Login.vue'),
      },
      {
        path:'signup',
        name:'SignUp',
        component:()=>import('@/views/account/pages/SignUp.vue'),
      },      
    ]
  },
]

const router = new VueRouter({
  routes
})

export default router
